@import '../abstracts/variables'; 
@import '../abstracts/mixins'; 
@import '../abstracts/font'; 
@import '../abstracts/keyFrame'; 
@import '../responsive/respon';
@import '../components/button'; 

.wp_footer {
    background: linear-gradient(200deg, $_000, #4E5C61, #73C9E6);
    // border-top-left-radius: 6rem;
    .footer_bottom,
    .footer {
        @include landscape_phones {
            flex-direction: column;
            // align-items: center;
        }
        form,
        ul,
        p {
            text-transform: capitalize;
            margin: .8rem 0;
        }
        padding: 4rem 0;
        bottom: 0;
        @include df(space-between, start);

        p {
            @include tablets {
                margin: .3rem 0;
            }
            @include phones {
            }
        }
        color: $fff;

        font-size: 13px;
        line-height: 20px;
        .col_4:first-child {
            padding-left: 0;
        }
        .col_4:last-child {
            padding-right: 0;
        }
        .col + .col {
            @include landscape_phones {
                border-top: 1px solid $fff;
            }
        } 
        .col {
            @include landscape_phones {
                width: 70vw !important;
                padding: 0 !important;
            }
            padding: 0 2rem;
            width: (100% / 4);
            @include df(center, start);
            flex-direction: column;
            a {
                color: $fff;
                list-style: none;
                padding: .1rem 0;
            }

            form {
                @include df(space-between, center);
                position: relative;
                width: 100%;
                &::after {
                    position: absolute;
                    content: "";
                    width: 100%;
                    left: 0;
                    bottom: 0;
                    height: 1px;
                    background: $fff;
                }
                .icon_email {
                    font-size: 1rem;
                    margin-right: 4px;
                }
                input {
                    width: calc(100% - 44.328px);
                    border: none;
                    outline: none;
                    color: $fff;
                    background: transparent;
                } 
                button {
                    @include df(center, center);
                    color: $fff;
                    background: transparent;
                    border: none;
                    outline: none;
                    cursor: pointer;
                    &:hover {
                        color: $eee;
                    }
                }
                padding: 10px 0;
            }

            h3 {
                text-transform: capitalize;
                margin: 0;
                font-size: 1rem;
                position: relative;
                @include tablets {
                    margin-top: 2rem;
                }
                @include phones {
                }
            }
            .animate {
                margin-top: 4px;
                height: 6px;
                width: 100%;
                background-color: #b0dae9b3;
                border-radius: 50px;
                position: relative;
                overflow: hidden;
                span {
                    width: 1rem;
                    border-radius: 50px;
                    position: absolute;
                    height: 100%;
                    animation: moveAnimateFooter 4s infinite;
                }
            }

            ul {
                li {
                    a {
                        position: relative;
                        &:hover {
                            color: $eee;
                        }
                        &:hover::after {
                            width: 100%;
                        }
                        &::after {
                            content: "";
                            background-color: $fff;
                            position: absolute;
                            height: 1px;
                            width: 0;
                            left: 0;
                            bottom: 0;
                            transition: cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.6s;
                        }
                    }
                }
            }

            .list-support {
                li {
                    margin-bottom: .3rem;
                    @include landscape_phones {
                        margin-bottom: 1rem;
                    }       
                }
            }

            .list_social-network {
                @include df(center, center);
                li {
                    width: 37px;
                    height: 33px;
                    margin: 0 .4rem;
                    a {
                        display: block;
                        width: 100%;
                        height: 100%;
                        border-radius: 50%;
                        &:hover::after {
                            width: 0;
                        }
                        background: #fff url(../../img/social-netWork.jpg) no-repeat;
                        background-position: 9.37% 35%;
                        background-size: 520px;
                        transition: linear 0.3s;
                        &:hover {
                            transform: translateY(-2px);
                        }
                    }
                }
                li:nth-child(2) {
                    a {
                        background-position: 20.77% 35%;
                    }
                }
                li:nth-child(3) {
                    a {
                        background-position: 32.27% 35%;
                    }
                }
                li:nth-child(4) {
                    a {
                        background-position: 43.77% 35%;
                    }
                }
            }
        }

        .col_4 {
            .trademark {
                line-height: 2rem;
                @include df(center, center);
                font-family: 'Josefin Sans', sans-serif;
                font-weight: 700;
                font-size: 1.7rem;
                text-align: center;
                @include phones {
                    padding: 10px 1px 4px;
                }
            }
        }
        .col_2 {
            width: (100% / 8);
        }

    }
    .footer_bottom {
        background: linear-gradient(359deg, $_000, transparent);
        padding-top: 0;
        .copy_right {
            @include smallest {
                width: unset;
            }
            &::after {
                position: absolute;
                content: "";
                height: 1px;
                background-color: $fff;
                width: 100%;
                top: 0;
                left: 0;
            }
            padding-top: 1rem;
            position: relative;
            width: 100%;
            text-align: center;
        }
    }
}