@import '../responsive/respon';
.btn-primary {
    background-image: linear-gradient(#0dccea, #0d70ea);
    border: 0;
    border-radius: 4px;
    box-shadow: rgb(0 0 0 / 30%) 0 5px 15px;
    box-sizing: border-box;
    color: #fff;
    cursor: pointer;
    font-family: Montserrat,sans-serif;
    font-size: .7em;
    padding: 10px 15px;
    text-align: center;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    @include tablets {
        font-size: .8rem;
    }
    @include phones {
    }
}

.btn-primary_2 {
    text-decoration: none;
    color: #222;
    border: 1px solid #222;
    border-radius: 50px;
    min-width: 80px;
    max-width: 120px;
    display: flex;
    word-wrap: break-word;
    justify-content: center;
    align-items: center;
    padding: 4px 8px;
    font-size: 14px;
    transition: .1s linear;
    &:hover {
        background-color: rgba(192, 192, 192, 0.573);
    }
}