@import './scss/abstracts/mixins';

.section {
    font-family: 'Poppins', sans-serif;
    .content {
        padding: 80px 0;

        .title {
            @include df(center, center);
            flex-direction: column;
            h2 {
                color: rgb(22, 22, 22);
                max-width: 700px;
                @include font_title;
                margin-bottom: 8px;
                text-transform: uppercase;
            }
            margin-bottom: 32px;
            text-align: center;
            p {
                text-transform: capitalize;
                @include body_text;
            }
        }
        .info {
            .cart {
                width: 400px;
            }
        }
    }
    .content + .content {
        padding-top: 0;
    }
}

* {
    line-height: 1.55;
}