@import './variables';
@import '../responsive/respon';

@mixin df($jc: flex-start, $ai: stretch) {
    display: flex;
    justify-content: $jc;
    align-items: $ai;
}

@mixin font_title {

    @include larger_desktops {
        font-size: $_size-title-Desktop;
    }

    @include desktops {
        font-size: $_size-title-Desktop;
    }

    @include tablets {
        font-size: $_size-title-Tablet;
    }

    @include phones {
        font-size: $_size-title-Mobile;
    }
    
}

@mixin font_sub-title {

    @include larger_desktops {
        font-size: $_size-sub-title-Desktop;
    }

    @include desktops {
        font-size: $_size-sub-title-Desktop;
    }

    @include tablets {
        font-size: $_size-sub-title-Tablet;
    }

    @include phones {
        font-size: $_size-sub-title-Mobile;
    }
    
}

@mixin body_text {
    font-size: $_size-body-all;
}