@import '../abstracts/variables'; 
@import '../abstracts/mixins'; 
@import '../abstracts/font'; 
@import '../abstracts/keyFrame'; 
@import '../responsive/respon';
@import '../components/button'; 

.sign_up-page {
    background: linear-gradient(185deg, #BCB3FF, #C2D4ED, #BBFCFF, #9EE8C1);
    min-height: 100vh;
    overflow: hidden;
    font-family: sans-serif;
    @include df(center, center);
    position: relative;
    .box-ballon {
        position: absolute;
        opacity: 0.3;
        pointer-events: none;
        user-select: none;
        animation: box-ballon_1 12s linear infinite;
    }
    .box-ballon:nth-child(1) {
        width: 60px;
        height: 40px;
        animation-delay: .2s;
        left: 5%;
        opacity: 0.3;
    }
    .box-ballon:nth-child(2) {
        width: 30px;
        opacity: 0.3;
        height: 30px;
        border-radius: 50%;
        left: 11%;
        animation-delay: 1s;
    }
    .box-ballon:nth-child(3) {
        width: 70px;
        opacity: 0.3;
        height: 30px;
        left: 40%;
        animation-delay: 2s;
    }
    .box-ballon:nth-child(4) {
        left: 70%;
        opacity: 0.3;
        width: 40px;
        height: 60px;
        border-radius: 10px;
        animation-delay: 3s;
    }
    .box-ballon:nth-child(5) {
        left: 90%;
        opacity: 0.3;
        width: 50px;
        height: 50px;
        animation-delay: 4s;
    }
    .box-ballon:nth-child(6) {
        left: 61%;
        opacity: 0.3;
        width: 70px;
        height: 30px;
        animation-delay: 5s;
    }
    .box-ballon:nth-child(7) {
        left: 20%;
        opacity: 0.3;
        width: 40px;
        height: 60px;
        border-radius: 10px;
        animation-delay: 6s;
    }
    .box-ballon:nth-child(8) {
        left: 83%;
        opacity: 0.3;
        width: 50px;
        height: 50px;
        animation-delay: 7s;
    }
    .container {
        display: flex;
        justify-content: center;
    }
    .form-wp {
        box-shadow: 0 20px 20px rgba(0, 0, 0, 0.4);
        overflow: hidden;
        position: relative;
        background:  url(../../img/br_signupPage.jpg) center no-repeat;
        max-width: 450px;
        width: 90%;
        min-height: 600px;
        @include phones {
            min-height: 530px;
        }
        @include df(center, center);
        .form-back {
            transform: rotateY(180deg);
        }
        .form {
            @include phones {
                padding-top: 24px;
            }
            padding-top: 56px;
            background-color: rgb(3 3 82 / 57%);
            @include df(start, center);
            flex-direction: column;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            transform-style: preserve-3d;
            backface-visibility: hidden;
            transition: cubic-bezier(0.075, 0.82, 0.165, 1) 1.5s;
            label,
            a {
                text-transform: capitalize;
            }
            a {
                margin-top: 16px;
                color: $fff;
                font-weight: normal;
                font-size: 14px;
            }
            h2 {
                font-family: 'Poppins', sans-serif;
                font-weight: 500;
                color: $fff;
                font-size: 24px;
                text-transform: uppercase;
                display: block;
                margin-bottom: 24px;
            }
            .form-group + .form-group {
                margin-top: 8px;
            }
            input:not([type="checkbox"]),
            button {
                font-family: 'Poppins', sans-serif;
                font-weight: 300;
                width: 300px;
                height: 32px;
                text-align: center;
                border: 2px solid $fff;
                outline: none;
                background: transparent;
                border-radius: 20px;
                color: $fff;
                @include smallest {
                    width: 80%;
                }
            }
            button {
                @include smallest {
                    height: 40px;
                }
            }
            .form-group {
                @include smallest {
                    width: 80%;
                }
                min-height: 58px;
                @include df(start, center);
                flex-direction: column;
                span {
                    display: block;
                    margin-top: 2px;
                    color: red;
                    font-size: 14px;
                }
                
                input:not([type="checkbox"]) {
                    @include smallest {
                        width: 80%;
                    }
                    transition: .3s ease;
                    &:focus {
                        border: 2px solid greenyellow;
                    }
                }

                &.controll {
                    align-items: start;
                    @include smallest {
                        align-items: center;
                    }
                    .strong {
                        margin-top: 4px;
                        display: flex;
                        align-items: center;
                        label {
                            margin-left: 4px;
                            color: $fff;
                            font-weight: normal;
                            font-size: 14px;
                        }
                        @include smallest {
                            margin-top: 16px;
                        }
                    }
                }
            }
            button {
                cursor: pointer;
                transition: all 0.2s linear;
                &:hover {
                    background-color: $fff;
                    color: $_000;
                }
            }
            button.submit {
                margin-top: 32px;
            }
            button.change {
                margin-top: 32px;
                @include smallest {
                    width: 65%;
                }
            }
        }
    }
    .back_prev {
        position: absolute;
        top: 30px;
        display: flex;
        align-items: center;
        z-index: 80;
        .icon {
            @include df( center, center);
        }
        left: 30px;
        font-size: 16px;
        @include phones {
            font-size: 12px;
        }
        color: #fff;
    }
}

::placeholder {
    color: $fff;
}