@import '../responsive/respon';
@import '../abstracts/font';
@import '../abstracts/mixins';
@import '../abstracts/variables';

.section_aboutus {
    .container {
        .content {
            .title {
                h2 {

                }
                p {

                }
            }
            .info {
                display: flex;
                justify-content: space-around;
                flex-wrap: wrap;
                .wp-cart {
                    width: 320px;
                    @include respond(387px) {
                        width: 96%;
                    }
                    margin: 0 20px;
                    margin-bottom: 20px;
                    text-align: center;
                    height: 400px;
                    border-radius: 15px;
                    background: $brWp_cart_ColorAbout;
                    overflow: hidden;
                    
                    .cart-img {
                        width: 100px;
                        height: 100px;
                        border-radius: 50%;
                        margin: 25px auto;
                        overflow: hidden;
                        border: 2px solid $borderColorAbout;
                        transition: ease .6s;
                        &:hover {
                            width: 100%;
                            border-radius: unset;
                            height: 100%;
                            margin-top: unset;
                            border: unset;
                        }
                        img {
                            width: 100%;
                            height: 100%;
                        }
                    }

                    .cart-name {
                        color: $fff;
                    }
                    .cart-des {
                        overflow: hidden;
                        text-overflow: ellipsis;
                        display: -webkit-box;
                        -webkit-box-orient: vertical;
                        -webkit-line-clamp: 3;
                        font-weight: 200;
                        color: $fff;
                        @include body_text;
                        margin: 15px 0;
                    }
                    .cart_list-social {
                        list-style: none;
                        display: flex;
                        justify-content: center;
                        margin: 20px 0 20px;
                        
                        li {
                            margin: 0 15px;
                            a {
                                color: #fff;
                                padding: 4px;
                                transition: .5s;
                                &:hover {
                                    color: $_color_primary_hover_cart_About;
                                }
                            }
                        }
                    }
                    .wp_btn {
                        width: 100%;
                        @include df(space-around, center);
                        button {
                            cursor: pointer;
                            padding: 6px 8px;
                            border-radius: 8px;
                            border: 1px solid $borderColorAbout;
                            background-color: transparent;
                            color: #fff;
                            transition: .5s;
                            &:hover {
                                background: $_color_primary_hover_cart_About;
                                border: 1px solid #fff;
                                color: #385f74;
                            }             
                        }       
                    }
                }
            }
        }
    }
}