@import '../abstracts/variables';
@import '../abstracts/mixins';
@import '../abstracts/font';
@import '../responsive/respon';

.weather_contain {
    @include respond(500px) {
        font-size: 12px;
    }
    position: relative;
    color: #fff;
    div.weather,
    div.panel {
        @include landscape_phones {
            position: relative;
            width: 100%;
            top: initial;
        }
    }
    .city-time,
    .temp,
    .city-time {
        // margin: 0 1em;
        h1 {
            margin: 0;
            margin-bottom: 0.2em;
            font-size: 3em;
        }
    }
    .temp {
        font-size: 7em;
        margin: 0;
    }
    .weather_info {
        img {
            display: block;
            margin: 0.5em 0;
        }
    }
    min-height: calc(100vh);
    background-image: url(../../img/weather/cold.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.3);
        z-index: 0;
    }
    h1, h3 {
        font-weight: 400;
    }
    .weather {
        box-sizing: border-box;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        flex-direction: column;
        @include df(space-between, flex-start);
        padding: 2em 3em 4em 3em;
        & > div {
            max-width: 50%;
            flex-wrap: wrap;
            @include df(space-between, center);
            @include landscape_phones {
                max-width: unset !important;
            }
        }
    }
    .panel {
        .optionChange {
            margin-top: 1em;
            select {
                color: #fff;
                border-radius: 2px;
                border: 1px solid #ccc;
                outline: none;
                height: 24px;
                background: transparent;
                & * {
                    color: #222;
                    margin: 2px 0;
                    background: #c3d2f1;
                    border: none;
                    padding: 2px 0;
                }
            }
        }
        box-sizing: border-box;
        position: absolute;
        width: 35%;
        height: 100%;
        top: 0;
        right: 0;
        background: rgba(110, 110, 110, .25);
        box-shadow: 0 8px 32px 0 rgba(0,0,0,0.3);
        backdrop-filter: blur(10px);
        -webkit-backdrop-filter: blur(10px);
        border: 1px solid rgba(255, 255, 255, .18);
        padding: 3em 2em;
        form{
            margin-bottom: 3em;
            .search {
                background: none;
                border: none;
                border-bottom: 1px solid #ccc;
                padding: 0 1em .5em 0;
                width: 80%;
                color: #fff;
                font-size: 1.1em;
                &:focus {
                    outline: none;
                }
                &::placeholder {
                    color: #ccc;
                }

            }
        }
        ul {
            &.cities {
                overflow-y: scroll;
                height: 140px;
            }
            border-bottom: 1px solid #ccc;
            li {
                padding: 0 0 1em 0;
                margin: 2em 0;
            }
            h4 {
                margin: 3em 0;
            }
        }
        .city {
            display: block;
            cursor: pointer;
            &:hover {
                color: #fff;
            }
        }
        .details li {
            @include df(space-between, center);

        }
    }
}