
@keyframes faceInVideo {
    from {
        transform: scale(.98);
    }
    to {
        transform: scale(1);
    }
}

@keyframes faceInNavMobile {
    from {
        transform: scale(0.1);
        border-radius: 50%;
    }
    to {
        transform: scale(1);
        border-radius: unset;
    }
}

@keyframes changeColorBorderLR {
    0% {
        border-left: 3px solid;
        border-right: 3px solid;
        border-left-color:#BA2EFF;
        border-right-color:#BA2EFF;
    }
    25% {
        border-left: 3px solid;
        border-right: 3px solid;
        border-left-color: #8F85ED;
        border-right-color:#79B6FF;
    } 
    50% {
        border-left: 3px solid;
        border-right: 3px solid;
        border-left-color:#79B6FF;
        border-right-color:#8F85ED;
    }
    75% {
        border-left: 3px solid;
        border-right: 3px solid;
        border-left-color:#61E8E8;
        border-right-color:#BA2EFF;
    }
    100% {
        border-left: 3px solid;
        border-right: 3px solid;                
        border-left-color:#6BFFA8;
        border-right-color:#6BFFA8;
    }
}


@keyframes moveAnimateFooter {
    0% {
        transform: translateX(-100%);
        background-color: rgba(126, 158, 202, 0.735);
    }
    20% {
        background-color: rgba(57, 195, 229, 0.885);
    }
    40% {
        background-color: rgba(188, 140, 221, 0.735);
    }
    60% {
        background-color: rgba(233, 135, 184, 0.735);
    }
    80% {
        background-color: rgba(30, 216, 230, 0.735);
    }
    100% {
        transform: translateX(calc(6rem));
        background-color: rgba(31, 213, 198, 0.735);
    }
}

@keyframes box-ballon_1 {
    0% {
        background-color:#BA2EFF;
        bottom: -10%;
        transform: rotate(0deg) translateX(0);
    }
    25% {
        background-color: #8F85ED;
        transform: rotate(90deg) translateX(-100px);
    } 
    50% {
        background-color:#79B6FF;
        transform: rotate(180deg)
    }
    75% {
        background-color:#61E8E8;
        transform: rotate(270deg) translateX(100px);
    }
    100% {
        background-color:#6BFFA8;
        bottom: 110%;
        transform: rotate(360deg) translateX(0);
    }
}