@import '../abstracts/variables'; 
@import '../abstracts/mixins'; 
@import '../abstracts/font'; 
@import '../abstracts/keyFrame'; 
@import '../responsive/respon';
@import '../components/button'; 

.header {
    position: relative;
    z-index: 1;
    width: 100%;
    height: 100vh;
    .wp-content {
        background-color: $_br_header_content;
        @include df(unset, center);
        color: $fff;
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        button {
        }
        .content {
            .title {
                text-transform: uppercase;
                h1 {
                    font-weight: 600;
                    font-family: 'Playfair Display', serif;
                    @include font_title
                }
                span {
                    margin: 1.2rem 0;
                    display: block;
                    font-family: 'Playfair Display', serif;
                    @include font_sub-title
                }
            }
            .des {
                letter-spacing: 2px;
                font-family: "Playfair Display", serif;
                margin: 1rem 0;
                @include body_text
            }
            button {
                transition: .3s ease;
                &:hover {
                    font-weight: 700;
                }
            }
        } 
    }
    .navbar_wp {
        &::after {
            content: "";
            position: absolute;
            height: 1px;
            background-color: $fff;
            left: 0;
            width: 0;
            bottom: 0;
            transition: all 0.4s ease;
        }
        width: 100vw;
        position: fixed;
        z-index: 99;
        left: 0;
        right: 0;
        color: $fff;
        padding: 10px 0%;
        transition: .3s linear;
        // @include df(space-between, center);
        .container {
            .navbar {
                @include df(space-between, center);
            }
        }
        a {
            color: $fff;
        }
        .trademark {
            @include df(center, center);
            font-family: 'Josefin Sans', sans-serif;
            font-weight: 700;
            text-align: center;
            @include phones {
                padding: 10px 1px 4px;
            }
            font-size: 1rem;
            line-height: 120%;
        }
        .list-item-nav {
            @include df(space-around, center);
            list-style: none;
            @include phones {
                display: none;
            }
            li {
                .link-item-nav {
                    letter-spacing: 1px;
                    text-transform: capitalize;
                    position: relative;
                    padding: 2px 4px;
                    margin: 0 16px;
                    font-size: 1rem;
                    font-family: 'Archivo Narrow', sans-serif;
                    @include df(center, center);
                    &::after {
                        content: "";
                        background-color: $fff;
                        position: absolute;
                        height: 2px;
                        width: 0;
                        bottom: 0;
                        transition: cubic-bezier(0.175, 0.885, 0.32, 1.275) .6s;
                    }
                    &:hover::after {
                        width: 100%;
                    }
                }
                .search-icon {
                    &:hover::after {
                        width: 0;
                    }
                }
            }
            li.icon-search {
                @include df(center, center);
                @include body_text;
                padding: 4px 8px;
                margin-right: 10px;
                cursor: pointer;
            }
            button {
                font-weight: bold;
                padding: 0;
                transform: scale(.8);
                a {
                    width: 100%;
                    height: 100%;
                    padding: 10px;
                    display: block;
                }
                &:hover {
                    font-weight: 300;
                }
            }
        }
        &.dark .trademark,
        &.dark .list-item-nav li button a,
        &.dark .list-item-nav li,
        &.dark .link-item-nav {
            color: $_000;
        }
        &.dark .list-item-nav li button {
            border-color: $_000;
        }
        &.scroll::after {
            width: 100%;
        }
        &.dark .link-item-nav::after,
        &.dark::after {
            background-color: $_000 !important;
        }
        &.dark {
            background: linear-gradient(60deg, #cbf5ff9b, #a8b3e6df);
            box-shadow: 0px 2px 7px #0000002e;
        }
    }
    .section-search {
        position: fixed;
        overflow: hidden;
        top: 58.38px;
        width: 100vw;
        height: 0px;
        background-color: transparent;
        @include df(center, center);
        flex-direction: column;
        z-index: 99;
        transition: all 0.4s linear;
        .search-scope {
            // width: 100%;
            // height: 100%;
            @include df(center, start);
            .wp_formSearch {
                width: 100%;
                height: 100%;
                @include df(center, end);
                flex-direction: column;
                input {
                    background-color: rgb(201 252 246 / 54%);
                    width: 100%;
                    text-align: center;
                    color: $_000;
                    outline: none;
                    padding: 0;
                    border: none;
                    height: 40px;
                    border-radius: 4px;
                    border-bottom-right-radius: 0px;
                }
            }
            button {
                margin-top: -2px;
                transform: scale(.9);
            }
        }
        .trending-scope {
            span {
                color: $_333;
                font-weight: 500;
                @include body_text;
            }
            @include df(center, center);
            width: 81%;
            background-color: $eee;
            border-bottom-left-radius: 50px;
            border-bottom-right-radius: 50px;
            .list_trending {
                padding: 8px;
                margin-left: 20px;
                @include df(center, center);
                list-style: none;
                li {
                    margin: 0 10px;
                }
                .link_trending-item {
                    @include body_text;
                    color: $_575757;
                }
            }
        }
        &.active {
            height: 80px;
        }
        @include phones {
            display: none;
        }
    }
    .iconBats-mobile {
        @include phones {
            @include df(center, center);
        }
        display: none;
        position: fixed;
        z-index: 100;
        color: $fff;
        top: 20px;
        right: 22px;
        width: 30px;
        height: 30px;
    }
    .navbar.mobile {
        display: none;
        animation: faceInNavMobile .4s ease;
    }
    @include phones {
        .navbar.mobile {
            position: fixed;
            z-index: 101;
            @include df(center, center);
            display: none;
            width: 100%;
            height: 100vh;
            background-color: rgba(0, 0, 0, 0.8);
            .list-item-nav {
                @include df(space-around, center);
                flex-direction: column;
                width: 80%;
                padding: 20px 0;
                li {
                    margin-bottom: 30px;
                    .link-item-nav {
                        height: 40px;
                        padding: 10px 30px;
                        font-size: 1rem;
                        color: $fff;
                        text-transform: capitalize;
                    }
                }
                button a {
                    padding: 14px 20px;
                    color: $fff;
                }
            }
            &.show {
                @include df(center, center);
            }
        }
    }
    .section-search-mobile {
        display: none;
        @include phones {
            background-color: rgba(0, 0, 0, 0.8);
            position: fixed;
            width: 100vw;
            height: 100vh;
            display: none;
            z-index: 101;
            .wp-formSearchMobile {
                flex-direction: column;
                @include df(start, center);
                border: 1px solid $fff;
                border-radius: 10px;
                height: 70vh;
                width: 70vw;
                input {
                    margin: 1rem 0;
                    border: none;
                    outline: none;
                    height: 32px;
                    border-radius: 2px;
                    width: 80%;
                }
                button {
                }
            }
            &.active {
                @include df(center, center);
            }
        }
    }
    .social-network {
        display: none;
    }   
    .controller {
        position: absolute;
        z-index: 1;
        bottom: 20px;
        left: 50%;
        list-style: none;
        display: flex;
        transform: translateX(-50%);
        @include phones {

        }
        @include tablets {
        }
        .controller-item {
            @include tablets {
                height: 20px;
                width: 30px;
                margin: 0 5px;
            }
            @include phones {
                height: 15px;
                width: 25px;
                margin: 0 5px;
            }
            margin: 0 3px;
            width: 12px;
            height: 12px;
            border-radius: 50px;
            background-color: #fff;
            transition: .4s ease;
            &:hover {
                cursor: pointer;
            }
            &.active {
                @include tablets {
                    width: 50px;
                }
                width: 20px;
            }
        }
    }
}

.video {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    animation: faceInVideo 1s;
}

.video.active {
    display: block;
}
