@import '../../scss/abstracts/mixins';
@import '../../scss/abstracts/variables';

.section_contact {
    h3,
    p {
        text-transform: capitalize;
        @include body_text;
    }
    background: #fff url(../../img/br_about_us.jpg) center no-repeat;
    position: relative;
    .overLay {
        background-color: rgba(50, 50, 50, 0.531);
        width: 100%;
        height: 100%;
        .content {
            h2 {
                color: $fff;
            }
            font-family: 'Poppins', sans-serif;
            color: $fff;
            @include df(center, center);
            flex-direction: column;
            .contactInfo {
                width: 100%;
                @include landscape_phones {
                    flex-direction: column;
                    align-items: start;
                }
                width: 100%;
                flex-wrap: wrap;
                @include df(space-around, center);
                .box {
                    .group_box {
                        .text {
                            margin-left: 16px;
                            h3 {
                                margin-bottom: 8px;
                            }
                            .email {
                                position: relative;
                                &::after {
                                    position: absolute;
                                    width: 100%;
                                    content: '';
                                    left: 0;
                                    bottom: 4px;
                                    height: 1px;
                                    background-color: $fff;
                                }
                            }
                        }
                        .icon {
                            width: 40px;
                            height: 40px;
                            border-radius: 50%;
                            background-color: $fff;
                            @include df(center, center);
                            color: $_000;
                            font-size: $_size-body-all;
                        }
                        @include df(sta/rt, start);
                    }
                    .group_box + .group_box {
                        margin-top: 24px;
                    }
                    .contactForm {
                        @include landscape_phones {
                            padding: 24px;
                        }   
                        h2 {
                            margin-bottom: 24px;
                            @include font_sub-title;
                        }
                        padding: 24px 48px;
                        background-color: $fff;
                        color: $_000;
                        .form-group {
                            position: relative;
                            margin-bottom: 24px;
                            .strong {
                                top: -16px;
                                position: absolute;
                                pointer-events: none;
                                left: 8px;
                                font-size: 16px;
                                transition: .2s linear;
                            }
                            input {
                                width: 100%;
                                font-size: 14px;
                                height: 30px;
                                line-height: 120%;
                                padding: 0;
                                margin: 0;
                                &:focus + .strong {
                                    top: -10px;
                                    font-size: 14px;
                                    left: 0;
                                }
                            }
                            .message {
                                font-size: 14px;
                                color: red;
                                pointer-events: none;
                                user-select: none;
                            }
                            input,
                            textarea {
                                border: none;
                                border-bottom: 2px solid $_333 !important;
                                outline: none;
                                width: 100%;
                            }
                            textarea {
                                &:focus + .strong {
                                    top: -16px;
                                    font-size: 14px;
                                    left: 0;
                                }
                            }
                            button {

                            }
                        }
                    }
                }
                .box + .box {
                    @include landscape_phones {
                        margin-top: 24px;
                    }
                }
            }
        }
    }
}