// breackPoints
$smallest: 0px;
$phones: 576px;
$landscape_phones: 768px;
$tablets: 992px;
$desktops: 1200px;
$larger_desktops: 1400px;

// self-defining breackPoints
@mixin respond ($screen_dimensions) {
    @media (max-width: $screen_dimensions) {
        @content
    }
};

// breackPoints default

@mixin smallest {
    @media (max-width: $phones) {
        @content
    };
};

@mixin phones {
    @media (max-width: $landscape_phones) {
        @content
    }
};

@mixin landscape_phones {
    @media (max-width: $tablets) {
        @content
    }
};

@mixin tablets {
    @media (max-width: $desktops) {
        @content
    }
};

@mixin desktops {
    @media (max-width: $larger_desktops) {
        @content
    }
};

@mixin larger_desktops {
    @media (min-width: $larger_desktops) {
        @content
    }
};

// -------------------
.container {
    @media (max-width: 575px) {
        width: 100%;
    }
    @media (min-width: 576px) {
        width: 540px;
    }
    @media (min-width: 768px) {
        width: 720px;
    }
    @media (min-width: 992px) {
        width: 960px;
    }
    @media (min-width: 1200px) {
        width: 1140px;
    }
    @media (min-width: 1400px) {
        width: 1320px;
    }
    margin: 0 auto;
}

.c-half {
    flex: 0 0 calc((100% / 12) * (1/2));
    max-width: calc((100% / 12) * (1/2));
}

