@import '../abstracts/variables';
@import '../abstracts/mixins';
@import '../abstracts/font';
@import '../responsive/respon';

.section_experiences {
    background: linear-gradient(248deg,#c8f3fa75, #bdeefc7c, #3df7fd77);
    .container {
        .content {
            &:first-child {
                padding-bottom: 0;
            }
            &:nth-child(2) {
                padding-top: 80px;
                padding-bottom: 0;
            }
            &+& {
                background-color: #fff;
            }
            .title{
                
            }
            .info {
                width: 100%;
                @include df(space-evenly, start);
                flex-wrap: wrap;
                .cart {
                    @include landscape_phones {
                        width: 240px;
                    }
                    width: 280px;
                    border: 2px solid transparent;
                    .icon {
                        font-size: 3rem;
                        color: $fff;
                    }
                    transition: .1s linear;
                    &:hover {
                        transform: translateY(-4px);
                        transform: translateX(8px);
                        transform: scale(1.01);
                        border-color:#374445;
                        box-shadow: -3px 6px 6px rgba(44, 43, 43, 0.6);
                    }
                    border-radius: 20px;
                    background-color: #87DBDE;
                    text-align: center;
                    padding: 24px;
                    margin: 0 16px 32px;
                    display: block;
                    color: $fff;
                    .heading {
                        text-transform: capitalize;
                        margin: 8px 0;
                        @include font_sub-title;
                    }
                    .icon-accordion {
                        width: 40px;
                        height: 40px;
                        border-radius: 50%;
                        @include df(center, center);
                        margin: 0 auto;
                        transition: all 0.2s ease;
                        &:hover {
                            color: $_333;
                            background: #c9e5f8;
                        }
                    }
                    .des {
                        @include body_text;
                    }
                }
            }
        }
        .customer_feedback {
            @include df(start, center);
            flex-wrap: wrap;
            .part_left {
                .content_left {
                    padding: 0 8px;
                    margin-bottom: 16px;
                }
                width: 40%;
                h2 {
                    @include font_sub-title;
                    margin-bottom: 8px;
                    text-transform: uppercase;
                }
                p {
                    @include body_text;
                    font-weight: 200;
                    font-style: italic;
                }
            }
            .part_right {
                position: relative;
                width: 60%;
                overflow: scroll;
                &::-webkit-scrollbar {
                    display: none;
                }
                & {
                    -ms-overflow-style: none;  /* IE and Edge */
                    scrollbar-width: none;  /* Firefox */
                }
                .icon {
                    position: absolute;
                    left: 50%;
                    transform: translateX(-50%);
                    font-size: 24px;
                    z-index: 1;
                }
                .icon_up {
                    top: 16px;
                }
                .icon_down {
                    bottom: -16px;
                }
                .content_right {
                    height: 340px;
                    @include df(space-around, center);
                    flex-wrap: wrap;
                    .cart {
                        margin: 8px 0;
                        background-color: #2a2c4a;
                        padding: 16px;
                        border-radius: 20px;
                        border: 2px solid transparent;
                        transition: 0.2s linear;
                        &:hover {
                            box-shadow: 0px 6px 6px rgb(44 43 43 / 60%);
                            border-color: rgb(74, 159, 142);
                            transform: translateY(-4px);
                        }
                        width: 240px;
                        display: block;
                        color: $fff;
                        .block_customer {
                            display: flex;
                            align-items: center;
                            .avatar {
                                background: $ccc url(../../img/customer.jpg) center no-repeat;
                                background-size: 90px;
                                border-radius: 50%;
                                width: 50px;
                                height: 50px;
                                overflow: hidden;
                                margin-right: 8px;
                            }
                        }
                        .feedback {
                            @include body_text;
                            font-weight: 200;
                            margin-bottom: 8px;
                        }
                    }
                }
            }
            .part_left,
            .part_right {
                @include landscape_phones {
                    width: 100%;
                    padding-left: 8px;
                    padding-right: 8px;
                }
            }
        }
        .video_cus {
            text-transform: uppercase;
            margin-bottom: 10px;
            text-align: center;
            background-color: #b9fafe;
        }
        .video_feedback {
            padding-top: 0;
            overflow-x: scroll;
            &::-webkit-scrollbar {
                height: 4px;
            }
            &-webkit-scrollbar-track {
                box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
            }
            &::-webkit-scrollbar-thumb {
                background-color: rgb(40, 167, 213);
                outline: 1px solid rgb(8, 98, 188);
              }
            .list_video {
                display: flex;
                .wp_video {
                    position: relative;
                    .btnControllVideo {
                        cursor: pointer;
                        position: absolute;
                        right: 4px;
                        bottom: 33%;
                        height: 24px;
                        font-size: 24px;
                        color: $fff;
                    } 
                    width: 450px;
                    margin: 0 16px;
                    video {
                    }
                    .info {
                        .main_content {
                            margin-top: 8px;
                            width: 100%;
                            @include df(space-between, center);
                            .place {
                                width: 50%;
                            }
                            .text {
                                overflow: hidden;
                                text-overflow: ellipsis;
                                display: block;
                                display: -webkit-box;
                                -webkit-box-orient: vertical;
                                -webkit-line-clamp: 2;
                                
                                width: 50%;
                                font-size: 12px;
                                font-weight: 200;
                            }
                        }
                        .infoPost {
                            margin-top: 8px;
                            font-size: 12px;
                            width: 100%;
                            @include df(space-between, center);
                            .icon {
                                margin-right: 4px;
                            }
                            .by,
                            .date {
                                display: flex;
                            }
                        }
                    }
                }
            }
        }
    }
}